<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-img
          :src="require('../assets/my.jpeg')"
          class="my-3"
          contain
          height="200"
        />
      </v-col>

      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Welcome to KMS APP
        </h1>

        <p class="subheading font-weight-regular">
          {{coin}}
          <br>please join our online
        </p>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

export default {
    name: 'HelloWorld',
    data(){
      return {
        coin : []
      }
    },
    methods : {
      getcoin() {
        this.coin = setInterval(() =>
        {
          console.log(this.coin)
          const API_URL = 'http://localhost:8080/data'
          this.$http.get(API_URL)
              .then(response => {
                const getdata = response.data
                console.log("getdata : " +getdata)
                this.coin = getdata
              })
        }, 30000
      )
      }
    },
  mounted() {
      this.getcoin()
  }


}
</script>
